<template>
    <div class="play-full-chart" @click="$emit('toggle-show-lyric')">
        <div class="mask">
            <img :class="{paused:paused}" :src="`${currentMusic.picUrl}?imageView&thumbnail=360y360`" />
        </div>
    </div>
</template>

<script>
export default {
    props:['currentMusic','paused'],

}
</script>

<style scoped lang='less'>
    .play-full-chart{
        width: 296px;
        height: 296px;
        margin: 0 auto;
        margin-top: 70px;
        box-sizing: border-box;
        position: relative;

        .mask{
            width: 296px;
            height: 296px;
            background-image: url('https://p5.music.126.net/obj/wo3DlcOGw6DClTvDisK1/4602256867/5855/63fb/325b/d7e4e3a….png?imageView=&quality=75&thumbnail=200y200');
            background-size: 100%;
            margin: 0 auto;

            img{
                width: 184px;
                height: 184px;
                border-radius: 50%;
                position: absolute;
                left: 50%;
                top: 50%;
                margin: -92px 0 0 -92px;
                box-shadow: 0 0 3px white;
                animation: playing 20s linear infinite;

                &.paused{
                    animation-play-state: paused;
                }
            }

            &::after{
                content: " ";
                position: absolute;
                left: 0;
                right: 0;
                top: 0;
                bottom: 0;
                z-index: 2;
                background: url('//s3.music.126.net/mobile-new/img/disc.png?d3bdd10…=') no-repeat;
                background-size: contain;
            }
        }
    }

    @keyframes playing {
        form{
            transform: rotate(0deg)
        }
        to{
            transform: rotate(360deg)
        }
    }
</style>