<template>
  <div class="playlist" v-if="songSheet">
    <div class="back" @click="upOneLevel">
      <i><svg t="1614134531928" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="1130" width="20" height="20"><path d="M298.83450838 561.87268107c-11.26157314-11.26157314-21.71874821-26.54513668-18.50115589-41.02430217-2.41319424-14.47916546 8.8483789-28.15393285 18.50115589-39.41550599L648.74767389 133.12850356c17.69675779-17.69675779 46.65508874-17.69675779 64.35184649-1e-8s17.69675779 46.65508874 0 64.35184654l-333.02080578 324.1724269L713.09952038 846.62960195c17.69675779 17.69675779 17.69675779 46.65508874-1e-8 64.35184652s-46.65508874 17.69675779-64.35184648 0l-349.91316551-349.1087674z" fill="#bfbfbf" p-id="1131"></path></svg></i>
    </div>
    <PlayListHeader :songSheet="songSheet" :avatarUrl='avatarUrl' :nickName='nickName' :lengths='lengths' />
    <h3 class="music-list">歌曲列表</h3>
    <PlayListMusic
      @update:currentMusic="$emit('update:currentMusic', $event)"
      @update:playList="$emit('update:playList', $event)"
      :playListSongs="playListSongs"
      :currentMusic="$attrs.currentMusic"
      :paused="$attrs.paused"
    />
  </div>
</template>

<script>
import PlayListHeader from "@/components/PlayListHeader.vue";
import PlayListMusic from "@/components/PlayListMusic.vue";
export default {
  components: {
        PlayListHeader,
        PlayListMusic,
    },

    data() {
        return {
            songSheet:{},
            trackIds:String,
            playListSongs:null,
            avatarUrl:null,
            nickName:null,
            lengths:null,
        }
    },

    created() {
        this.demand();
    },

    beforeRouteUpdate (to, from, next) {
    // 在当前路由改变，但是该组件被复用时调用
    // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
    // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
    // 可以访问组件实例 `this`
    this.demand();
    next()
  },

  beforeRouteEnter (to, from, next) {
    next(vm => {
      // 通过 `vm` 访问组件实例
      vm.demand();
    })
  },

    methods: {
      demand(){
        this.$root.isShowLoading = true;

        this.axios
        .get("/playlist/detail?id="+this.$router.history.current.query.id)
        .then((response) => {
          this.songSheet = response.data.playlist;
          this.avatarUrl = response.data.playlist.creator.avatarUrl;
          this.nickName = response.data.playlist.creator.nickname;
          this.lengths = response.data.playlist.creator.nickname.length;
        
          this.trackIds = response.data.playlist.trackIds.slice(0, 20).map((e) => e.id).join();

          this.axios.get("/song/detail?ids=" + this.trackIds).then((response) => {
              this.playListSongs = response.data.songs
              .map((item)=>{
                return {
                  id: item.id,
                  name: item.name,
                  picUrl: item.al.picUrl,
                  isShowPlayListIndex:true,
                  song: {
                    artists:item.ar
                  },
                }
              });
            })

        })
        .finally(() => {
            this.$root.isShowLoading = false;
        });
      },

      upOneLevel(){
          window.history.back();
      },
    },
}
</script>

<style lang='less'>
.playlist {
  position: relative;

  .back{
    position: absolute;
    top: 4px;
    left: 4px;
    z-index: 999;
  }
  .music-list {
    width: 100%;
    height: 23px;
    line-height: 23px;
    padding: 0 10px;
    font-size: 12px;
    color: #666;
    background-color: #eeeff0;
    font-weight: 400;
  }
}
</style>