<template>
    <div class="play">
        <audio
            :src="
                'https://music.163.com/song/media/outer/url?id=' +
                currentMusic.id +
                '.mp3'
            "
            autoplay
            ref="audio"
        ></audio>
        <transition
        name="custom-classes-transition"
        enter-active-class="animate__animated animate__slideInUp"
        leave-active-class="animate__animated animate__slideOutDown"
        >
            <div class="play-bar" v-show="isShowPlayBar">

                <div class="left" @click="isShowPlayBar = false">
                    <div class="mask">
                        <img :class="{paused:paused}" :src="`${currentMusic.picUrl}?imageView&thumbnail=246x0`" />
                    </div>

                    <h5>
                        {{currentMusic.name}}
                        <span> - {{currentMusic.song.artists[0].name}}</span>
                    </h5>
                </div>

                <div class="control" @click="togglePlayState">
                    <canvas ref="circle" width="50" height="50"></canvas>

                    <span class="icon" :class="[paused ? paused_music : playing_music]" >
                    </span>
                </div>

                <div class="play-list" @click="isShowPlayBarCurrentList = !isShowPlayBarCurrentList">
                    <svg t="1608535131658" class="icon" viewBox="0 0 1024 1024" version="1.1" xmlns="http://www.w3.org/2000/svg" p-id="40367" width="48" height="48"><path d="M471.04 308.224h302.592v27.136H471.04zM291.84 502.784h481.792v30.72H291.84zM288.768 716.288h481.792v30.72H288.768zM306.688 418.816c-6.656 0-12.288-2.56-16.384-7.68-4.096-5.12-6.656-12.288-6.656-19.968V254.464c0-7.68 2.56-14.848 6.656-19.968s9.728-7.68 16.384-7.68c5.12 0 10.24 1.536 15.36 4.608L424.96 300.032c9.216 5.632 14.336 13.824 14.336 23.04 0 9.216-5.12 17.408-14.336 23.04L322.56 414.208c-5.632 3.072-10.752 4.608-15.872 4.608z m0 0" fill="#000" p-id="40368">
                        </path>
                    </svg>
                </div>
                    <PlayCurrentList 
                        v-show="isShowPlayBarCurrentList"
                        @update:currentMusic="$emit('update:currentMusic',$event)"
                        @update:playList="$emit('update:playList',$event)" 
                        @remove-music="$emit('remove-music',$event)"
                        @is-show-play-current-list="isShowPlayBarCurrentList = !isShowPlayBarCurrentList"
                        :newPlayList='newPlayList'
                        :currentMusic="currentMusic"
                        :paused="$attrs.paused"
                    />
            </div>
    </transition>

        <transition
        name="custom-classes-transition"
        enter-active-class="animate__animated animate__fadeIn"
        leave-active-class="animate__animated animate__fadeOut"
        >
            <div class="play-full" v-show="!isShowPlayBar">
                <div class="play-full-mask">
                    <img :src="`${currentMusic.picUrl}?imageView&thumbnail=369x0`">
                </div>
                <PlayFullHeader :currentMusic='currentMusic'  @show-play-bar="isShowPlayBar = true" />

                <template>
                    <transition
                    name="custom-classes-transition"
                    enter-active-class="animate__animated animate__fadeIn"
                    leave-active-class="animate__animated animate__fadeOut"
                    >
                        <PlayFullLyric 
                            v-show="isShowLyric"
                            @toggle-show-lyric="isShowLyric=!isShowLyric"
                            :currentMusic="currentMusic"
                            :currentTime="currentTime"
                            @current-lyric-times="$refs.audio.currentTime = $event"
                        />
                    </transition>

                    <transition
                    name="custom-classes-transition"
                    enter-active-class="animate__animated animate__fadeIn"
                    leave-active-class="animate__animated animate__fadeOut"
                    >
                        <PlayFullChart 
                            v-show="!isShowLyric"
                            @toggle-show-lyric="isShowLyric=!isShowLyric" 
                            :currentMusic='currentMusic'
                            :paused='paused'
                        />
                        
                    </transition>
                    
                </template>

                <PlayFullFooter 
                    :currentTime="currentTime"
                    @update:currentTime="$refs.audio.currentTime = $event"
                    :duration="duration"
                    :paused="paused"
                    @toggle-play-state="togglePlayState"
                    @play-next="playNext"
                    @play-last="playNext"
                    @update:loop="playCurrent = $event"
                    @is-show-play-current-list="isShowPlayCurrentList = !isShowPlayCurrentList"
                />

                <PlayCurrentList 
                    v-show="isShowPlayCurrentList"
                    @update:currentMusic="$emit('update:currentMusic',$event)"
                    @update:playList="$emit('update:playList',$event)" 
                    @remove-music="$emit('remove-music',$event)"
                    @is-show-play-current-list="isShowPlayCurrentList = !isShowPlayCurrentList"
                    :newPlayList='newPlayList'
                    :currentMusic="currentMusic"
                    :paused="$attrs.paused"
                />
            </div>
        </transition>
    </div>
</template>

<script>
import PlayFullHeader from "@/components/PlayFullHeader.vue";
import PlayFullChart from "@/components/PlayFullChart.vue";
import PlayFullLyric from "@/components/PlayFullLyric.vue";
import PlayFullFooter from "@/components/PlayFullFooter.vue";
import PlayCurrentList from "@/components/PlayCurrentList.vue";

export default {
    props:['currentMusic','playList','currentIndex','newPlayList'],
    
    components: {
        PlayFullHeader,
        PlayFullChart,
        PlayFullLyric,
        PlayFullFooter,
        PlayCurrentList,
    },
    
    data() {
        return {
            paused: null,
            isShowLyric: false,
            isShowPlayBar: true,
            duration: 0,
            currentTime: 0,
            paused_music : 'paused_music',
            playing_music : 'playing_music',
            playCurrent: 0,
            isShowPlayCurrentList:false,
            isShowPlayBarCurrentList:false,
        }
    },

    mounted() {

        let audio = this.$refs.audio;

        audio.addEventListener("pause", () => {
            this.paused = true;
        });

        audio.addEventListener("play", () => {
            this.paused = false;
        });

        audio.addEventListener("durationchange",()=> {
            this.duration = audio.duration;
        });

        audio.addEventListener("timeupdate",()=> {
            this.currentTime = audio.currentTime;
            
            this.drawCircle(this.currentTime,this.duration);
        });

        audio.addEventListener("ended",()=> {
            this.playNext();
        });
    },

    methods: {
        // 圆
        drawCircle(n,total){
            let canvas = this.$refs.circle;
            let ctx = canvas.getContext("2d");

            ctx.clearRect(0, 0, 50, 50);
            ctx.beginPath();
            ctx.strokeStyle = "#ccc";
            ctx.arc(25,25,16,0,Math.PI * 2,false);
            ctx.stroke();
            ctx.closePath();

            ctx.beginPath();
            ctx.strokeStyle = "#000";
            ctx.arc(
                25,
                25,
                16,
                Math.PI * -0.5,
                Math.PI * ((n/total) * 2 - 0.5),
                false
                );
            ctx.stroke();
            ctx.closePath();
            
        },

        togglePlayState(){
            let audio = this.$refs.audio;
            if(this.paused){
                audio.play();
            }else{
                audio.pause();
            }
        },

        calculateNext(plays){
            let nextIndex;
            
            // 下一首
            if( plays == 'next' ){
                if(this.currentIndex < this.playList.length - 1){
                    nextIndex = this.currentIndex + 1;
                }else{
                    nextIndex = 0;
                }
            // 上一首
            }else if( plays == 'last' ){
                if(this.currentIndex > 0){
                    nextIndex = this.currentIndex - 1;
                }else{
                    nextIndex = this.playList.length - 1;
                }
            }

            return nextIndex;
        },

        playNext(plays){
            if( this.playCurrent == 0  &&  plays!='last' && plays!='next' ){
                plays='next';
            }

            if( this.playCurrent == 1 || this.playCurrent == 2 ){
                this.playNextLoop();
            }

            let nextMusic;
            if( (plays=='next' || plays=='last') && this.playCurrent == 0 ){
                nextMusic = this.calculateNext(plays);
            }else{
                nextMusic = this.playNextLoop();
            }

            if( nextMusic == this.currentIndex ){
                this.$refs.audio.play();
                return;
            }else{
                    this.$emit("update:currentMusic",{
                    item: this.playList[nextMusic],
                    index: nextMusic,
                })
            }
        },

        playLast(){
            let nextMusic = this.calculateNext();

            this.$emit("update:currentMusic",{
                item: this.playList[nextMusic],
                index: nextMusic,
            })
        },

        playNextLoop(){
            let loopMusic;
            // 单曲循环
            if( this.playCurrent == 1 ){
                loopMusic = this.currentIndex;
            // 随机播放
            }else if( this.playCurrent == 2 ){
                do {
                    loopMusic = Math.floor(Math.random()*this.playList.length);
                } while (loopMusic === this.currentIndex);
            }
            return loopMusic;
        },
    },

    watch: {
        paused: function (n) {
            this.$emit("update:paused", n);
        },
    },
}
</script>

<style scoped lang="less">
    .play-bar{
        position: fixed;
        bottom: 0;
        left: 0;
        z-index: 9;
        width: 100%;
        background: white;
        display: flex;
        box-shadow: 0 0 5px 0 rgba(0, 0, 0, 0.15);

        .left{
            flex: 1;
            display: flex;

            .mask{
                width: 40px;
                height: 40px;
                background: #000;
                border-radius: 50%;
                margin: 8px 0 0 8px;

                img{
                    width: 28px;
                    height: 28px;
                    border-radius: 50%;
                    margin: 6px;
                    animation: playing 20s linear infinite;

                    &.paused{
                        animation-play-state: paused;
                    }
                }

            }
            
            h5{
                flex: 1;
                line-height: 50px;
                margin-left: 10px;
                display: -webkit-box;
                -webkit-box-orient: vertical;
                -webkit-line-clamp: 1;
                overflow: hidden;
                text-overflow: ellipsis;
                color: #000;
                font-size: 16px;

                span{
                    font-size: 14px;
                    color: rgb(121, 115, 115);
                }
            }
        }

        .control{
            position: relative;

            span.icon{
                position: absolute;
                top: 3px;
                left: 2px;
                width: 36px;
                height: 36px;

                &.paused_music::before{
                    content: "";
                    display: block;
                    width: 25px;
                    height: 25px;
                    background: url(../images/start.png) no-repeat;
                    background-size: 60%;
                    position: absolute;
                    top: 15px;
                    left: 16px;
                }

                &.playing_music::before{
                    content: "";
                    display: block;
                    width: 25px;
                    height: 25px;
                    background: url(../images/stop.png) no-repeat;
                    background-size: 60%;
                    position: absolute;
                    top: 15px;
                    left: 16px;
                }
            }
        }
    }

    .play-full{
        width: 100vw;
        height: 100vh;
        position: fixed;
        top: 0;
        left: 0;
        z-index: 10;

        .play-full-mask{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 0;
            left: 0;
            z-index: -2;

            img{
                width: 100%;
                height: 100%;
                opacity: 1;
                transform: scale(1.5);
                transform-origin: center center;
                filter: blur(30px);
            }

            &::after{
                content: " ";
                position: absolute;
                left: 0;
                right: 0;
                bottom: 0;
                top: 0;
                background-color: rgba(0,0,0,.5);
            }
        }
    }

    @keyframes playing {
        form{
            transform: rotate(0deg)
        }
        to{
            transform: rotate(360deg)
        }
    }
</style>