<script>
// import HomeHeard from "@/views/HomeHeard.vue";
import HomeNav from "@/components/HomeNav.vue";
import Play from "@/components/Play.vue";
export default {
  components: {
    HomeNav,
    Play,
    // HomeHeard
  },

  data() {
    return {
      currentMusic: null,
      paused : null,
      playList: [],
      currentIndex: 0,
      newPlayList: [],
    }
  },

  methods: {

    removeMusic(id){
      this.newPlayList = this.newPlayList.filter((item)=>{
          return item.id !== id;
      })
    }
  },

  watch:{
    playList(n){
      this.newPlayList = n.map((item)=>{
        return {
            id: item.id,
            name: item.name,
            picUrl: item.picUrl,
            isShowRemove:true,
            song: item.song,
        }
      })
    }
  }
};
</script>

