<script>
export default {
    props:['currentTime','duration','paused'],

    data() {
        return {
            control: 0,
        }
    },

    computed:{
        footerCurrentTime(){
            let minute;
            let second;
            minute = Math.floor(this.currentTime/60) < 10 ? "0"+ Math.floor(this.currentTime/60) : Math.floor(this.currentTime/60);
            second = Math.floor(this.currentTime%60) < 10 ? "0"+ Math.floor(this.currentTime%60) : Math.floor(this.currentTime%60);

            return minute + ":" + second;
        },
        footerDuration(){
            let minute;
            let second;
            minute = Math.floor(this.duration/60) < 10 ? "0"+ Math.floor(this.duration/60) : Math.floor(this.duration/60);
            second = Math.floor(this.duration%60) < 10 ? "0"+ Math.floor(this.duration%60) : Math.floor(this.duration%60);

            return minute + ":" + second;
        },
    },

    methods: {
        getValue(e){
            this.$emit("update:currentTime",e.target.value);
        },

        isShowSongs(){
            if( this.control >= 2 ){
                this.control = 0;
            }else{
                this.control++;
            }

            this.$emit("update:loop", this.control);
        }
    },
}
</script>

<style lang="less" >
    li{
        list-style: none;
    }

    .play-full-footer{
        position: absolute;
        bottom: 0;
        left: 0;
        width: 100%;
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;

        .play-list-heard{
            width: 100%;
            height: 40px;
            ul{
                width: 100%;
                display: flex;
                justify-content: space-around;
                li{
                    flex: 2;
                    width: 20%;
                    text-align: center;
                    color: white;

                }

                li:nth-of-type(3){
                    div{
                        width: 30px;
                        height: 30px;
                        text-align: center;
                        line-height: 30px;
                        border: 1px solid white;
                        border-radius: 50%;
                        margin: 0 auto;
                    }
                }

                li:nth-of-type(5){
                    padding-top: 10px;
                    box-sizing: border-box;
                }
            }
        }

        .progress{
            width: 70%;
            height: 2px;
            background: rgb(179, 176, 176);
            position: relative;
            left: 0;
            top: 50%;
            margin: 0 auto;
            margin-top: -1px;
            border-radius: 1px;
            margin: 20px 0;
            color: white;
            font-size: 14px;
            display: flex;

            span.currentTime{
                position: absolute;
                top: -8px;
                left: -45px;
            }
            span.duration{
                position: absolute;
                top: -8px;
                right: -45px;
            }

            p{
                flex: 1;

                input{
                width: 100%;
                position: absolute;
                top: 0;
                left: 0;
                z-index: 99;
                opacity: 0;
                }

                span{
                    display: block;
                    position: absolute;
                    top: 0;
                    left: 0;
                    width: 0%;
                    height: 100%;
                    background: white;
                    z-index: 1;
                    border-radius: 1px;
                    
                    &::after{
                        content: "";
                        display: block;
                        width: 6px;
                        height: 6px;
                        border-radius: 50%;
                        background: white;
                        position: absolute;
                        top: 0;
                        right: 0;
                        margin-top: -2px;
                        margin-right: -3px;
                    }
                }
            }
        }

        .play-list-footer{
            width: 100%;
            height: 48px;
            margin: 0 0 10px 0;
            ul{
                width: 100%;
                height: 100%;
                display: flex;
                justify-content: space-around;

                li{
                    flex: 1;
                    width: 20%;
                    height: 100%;
                    line-height: 48px;
                    text-align: center;
                    color: white;
                    margin-top: 10px;
                    
                }

                li:nth-of-type(1){
                    margin-top: 0;
                    ol{
                        margin: 10px 0 0 20px;
                    }
                }

                li:nth-of-type(3){
                    margin-top: 0;
                    ol{
                        margin-left: 12px;
                    }
                }

                li:nth-of-type(5){
                    margin-top: 0;
                }

                ol.toggle{
                    width: 100%;
                    height: 100%;
                    position: relative;

                    li{
                        width: 100%;
                        position: absolute;
                        top: 0;
                        left: -12px;
                    }

                    li.playings{
                        top: 4px;
                    }
                }
            }
        }
    }
</style>