<template>
    <section class="play-list-header">
        <div 
            class="back-ground"
            :style="{backgroundImage:url}"
        ></div>

        <div class="song-sheet">
            <div class="song-sheet-img">
                <img :src="`${songSheet.coverImgUrl}?imageView=1&type=webp&thumbnail=252x0`" />
                <span>歌单</span>
                <i>{{playCount}}万</i>
            </div>

            <div class="song-sheet-item">
                <h3>{{songSheet.name}}</h3>
                <div class="song-sheet-singer">
                    <img :src="`${avatarUrl}?imageView=1&type=webp&thumbnail=60x0`" />
                    <span>{{nickname}}</span>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
    props:['songSheet','avatarUrl','nickName','lengths'],
    
    computed:{
        playCount(){
            return Math.floor(this.songSheet.playCount/10000 * 10)/10;
        },

        url(){
            return "url("+this.songSheet.coverImgUrl+")"
        },

        nickname(){
            if( this.lengths <= 6 ){
                return this.nickName;
            }else{
                return this.nickName.slice(0,7) + "...";
            }
        },
    },
};
</script>

<style scoped lang='less'>
    .play-list-header{
        width: 100vw;
        display: block;
        padding: 30px 10px 30px 15px;
        position: relative;
        overflow: hidden;

        .back-ground{
            width: 100%;
            background-repeat: no-repeat;
            background-size: cover;
            background-position: 50%;
            -webkit-filter: blur(20px);
            filter: blur(20px);
            -webkit-transform: scale(1.5);
            -ms-transform: scale(1.5);
            transform: scale(1.5);
            position: absolute;
            left: 0;
            top: 0;
            right: 0;
            bottom: 0;
            z-index: 1;

            &::after{
                content: '';
                background-color: rgba(0,0,0,.25);
                position: absolute;
                left: 0;
                top: 0;
                right: 0;
                bottom: 0;
                z-index: 1;
            }
        }

        .song-sheet{
            display: flex;
            position: relative;
            z-index: 2;

            .song-sheet-img{
                width: 126px;
                height: 126px;
                position: relative;
                background-color: #e2e2e3;
                img{
                    width: 100%;
                    vertical-align: middle;
                    border: 0;
                }

                span{
                    padding: 0 8px;
                    height: 17px;
                    color: #fff;
                    font-size: 9px;
                    text-align: center;
                    line-height: 17px;
                    background-color: rgba(217,48,48,.8);
                    border-top-right-radius: 17px;
                    border-bottom-right-radius: 17px;
                    position: absolute;
                    top: 10px;
                    left: 0;
                    z-index: 3;
                }

                i{
                padding-left: 15px;
                color: #fff;
                font-size: 12px;
                font-style: normal;
                background-position: 0;
                background-repeat: no-repeat;
                background-size: 11px 10px;
                text-shadow: 1px 0 0 rgba(0,0,0,.15);
                position: absolute;
                right: 2px;
                top: 0;
                z-index: 3;
                background-image: url('data:image/svg+xml;base64,');
                }

                &::after{
                    content: " ";
                    position: absolute;
                    left: 0;
                    top: 0;
                    width: 100%;
                    height: 18px;
                    z-index: 2;
                    background-image: -webkit-linear-gradient(left,rgba(0,0,0,0),rgba(0,0,0,.2));
                    background-image: linear-gradient(90deg,rgba(0,0,0,0),rgba(0,0,0,.2));
                }
            }

            .song-sheet-item{
                flex: 1 1 auto;
                width: 1%;
                margin-left: 16px;

                h3{
                    padding-top: 1px;
                    font-size: 17px;
                    line-height: 1.3;
                    color: #fefefe;
                    height: 44px;
                    white-space: normal;

                    display: -webkit-box;
                    -webkit-box-pack: center;
                    overflow: hidden;
                    text-overflow: ellipsis;
                    -webkit-line-clamp: 2;
                    -webkit-box-orient: vertical;
                }

                .song-sheet-singer{
                    position: relative;
                    margin-top: 20px;
                    color: hsla(0,0%,100%,.7);
                    overflow: hidden;
                    text-overflow: ellipsis;
                    white-space: nowrap;
                    word-break: normal;

                    img{
                        width: 30px;
                        height: 30px;
                        border: 0;
                        border-radius: 50%;
                        vertical-align: middle;
                        margin-right: 5px;
                        vertical-align: middle;
                    }

                    span{
                        font-size: 14px;
                    }
                }
            }
        }
    }
</style>