<template>
    <li
    class="song-item" 
    @click="$emit('update:currentMusic',{item,index});$emit('update:playList');isliColor=!isliColor"
    :class="{ showIndex:item.isShowHotIndex}"
    >
    <!-- :style="liColor" -->
        <div v-if="item.isShowHotIndex" class="hotMusicIndex" :style="{color: indexColor}">
            {{hotIndex}}
        </div>

        <div v-if="item.isShowPlayListIndex" class="PlayListMusicIndex">
            {{index+1}}
        </div>

        <div class="subject" v-if="item.isShowRemove">

            <!-- 图 -->
            <div class="icon play-current-list-icon" >
                <span 
                v-if="(currentMusic && currentMusic.id === item.id)"
                :class="[ paused ? pauseds : playings]"
                ></span>
                <!-- <span class="suspend" v-else></span> -->
            </div>

            <!-- 文字 -->
            <div class="info play-current-list-info">
                <h5>{{item.name}} 
                    <span style="marginLeft:8px;marginRight:8px">-</span>
                    <span v-for="(artist,index) in item.song.artists" :key="index">
                        <template v-if="index">、</template>{{artist.name}}
                    </span>
                </h5>
                
            </div>

            <div class="remove" @click.stop="$emit('remove-music',item.id)">
                <i class="play-current-list-i"></i>
            </div>

            
        </div>

        <div class="subject lineHeight" v-else>

            <div class="info">
                <h5>{{item.name}}</h5>
                <p>
                    <span v-for="(artist,index) in item.song.artists" :key="index">
                        <template v-if="index">/</template>{{artist.name}}

                        <span v-if="item.isShowIntroduce"> - {{item.introduce}}</span>
                        <span v-else> - {{item.name}}</span>

                    </span>
                </p>
            </div>
            
            <div class="icon">
                <span 
                v-if="currentMusic && currentMusic.id === item.id"
                :class="[ paused ? pauseds : playings]"
                ></span>
                <span class="suspend" v-else></span>
            </div>
        
        </div>
    </li>
</template>

<script>
export default {
    props:{
        item:{
            type:Object,
            
        },
        currentMusic:{
            type:Object
        },
        paused:{
            type:Boolean
        },
        index:{
            type:null
        }
    },

    data() {
        return {
            pauseds : 'pauseds',
            playings : 'playings',
            indexColor : this.index <= 2 ? '#df3436':'',
            hotIndex : this.index < 9 ? "0"+(this.index+1) : this.index+1,
            isliColor:false,
        }
    },

}
</script>

<style lang="less">
    li.song-item{
        width: 100vw;
        color: rgb(109, 107, 107);
        border-bottom: 1px solid rgba(207, 207, 207,0.5);
        padding-top: 4px;
        padding-left: 8px;
        padding-bottom: 3px;
        box-sizing: border-box;
        display: flex;

        &.showIndex{
            padding-left: 10px;
        }
        
        &.liColor{
            color: black;
        }

        .hotMusicIndex,.PlayListMusicIndex{
            width: 40px;
            height: 50px;
            line-height: 50px;
            text-align: center;
            color: #857a7a;
            font-size: 18px;
            margin-left: -5px;
        }

        .subject{
            width: 100%;
            height: 40px;
            display: flex;

            .info{
                flex: 1;

                h5{
                    font-size: 17px;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    text-overflow: ellipsis;
                }

                p{
                    font-size: 14px;
                    color: #888;
                    display: -webkit-box;
                    -webkit-box-orient: vertical;
                    -webkit-line-clamp: 1;
                    overflow: hidden;
                    text-overflow: ellipsis;

                    span{
                        font-size: 12px;
                    }

                    &::before{
                        content: '';
                        display: inline-block;
                        width: 12px;
                        height: 8px;
                        background: url(../images/icon.png) no-repeat;
                        background-size: 166px 97px;
                        margin-right: 4px;
                    }
                }
            }

            .play-current-list-info{
                line-height: 40px;
                h5{
                    font-size: 16px;
                    span{
                        font-size: 13px;
                        color: #ccc;
                    }
                }
            }

            .icon{
                width: 50px;
                height: 50px;

                span{
                    margin: 12px 0 0 18px;
                }

                .playings,.pauseds{
                    display: block;
                    width: 22px;
                    height: 22px;
                }

                .playings{
                    background: url(../images/playing.gif) no-repeat center;
                    background-size: 150%;
                }

                .pauseds{
                    background: url(../images/paused.png) no-repeat center;
                    background-size: 150%;
                }

                .suspend{
                    display: block;
                    width: 22px;
                    height: 22px;
                    background: url(../images/icon.png) no-repeat -24px 0;
                    background-size: 166px 97px;
                }
            }

            .play-current-list-icon{
                width: 30px;
                height: 40px;
                span{
                    margin: 10px 0 0 0;
                }
            }

            .play-current-list-i{
                width: 12px;
                height: 12px;
                background-image: url('data:image/svg+xml;base64,');
                display: inline-block;
                vertical-align: middle;
                background-position: 0 0;
                background-size: contain;
                background-repeat: no-repeat;
                margin: 10px 8px 0 5px;
            }
        }

    }
</style>