<template>
  <div class="search">

    <div class="search-input">
      <i></i>
      <input 
        type="text" 
        placeholder='搜索歌曲、歌手、专辑' 
        v-model="msg" 
        @keyup.enter="details"
        />
      <span 
        class="close" 
        v-show="isShowClose"
        @click="msg=''"
        >
        <i></i
        ></span>

    </div>

    <!-- 搜索提示 -->
    <div class="search-list" v-if="isShowSearchList">
      <h5>搜索"{{msg}}"</h5>
      <ul>
        <SearchList v-for="(item, index) in searchProposal" :key="index" :item='item' @search-list='ItemFirst' />
          <!-- {{item.keyword}} -->
      </ul>
    </div>

    <div class="details" v-else-if="isShowDetails">
      <h3 v-if="bestMatch.artist || bestMatch.album">最佳匹配</h3>
      <ul class="search-result">
        <SearchResult
          :bestMatch="bestMatch"
        />
      </ul>

      <SearchMusic 
        @update:currentMusic="$emit('update:currentMusic',$event)"
        @update:playList="$emit('update:playList',$event)" 
        :songs="songs"
        :currentMusic="$attrs.currentMusic"
        :paused="$attrs.paused"
      />
      
    </div>
      
      <!-- 热门搜索 -->
    <div  v-else>
      <div class="hot-search">
        <h6>热门搜索</h6>
        <ul>
          <SearchHot 
            v-for="(item, index) in searchHot" 
            :key="index" 
            :item="item"
            @item-first="ItemFirst"
          />
        </ul>
      </div>

      <div class="historical-records">
        <!-- 历史记录 -->
        <ul>
          <SearchHistoricalRecords 
            v-for="(item, index) in historical" 
            :key="index" 
            :item="item"
            @item-first="ItemFirst"
            @remove-list="removeList"
          />
        </ul>
      </div>
    </div>

  </div>
</template>

<script>
import SearchHot from "@/components/SearchHot.vue";
import SearchHistoricalRecords from "@/components/SearchHistoricalRecords.vue";
import SearchList from "@/components/SearchList.vue";
import SearchResult from "@/components/SearchResult.vue";
import SearchMusic from "@/components/SearchMusic.vue";

// 使用lodash
import _ from "lodash"

export default {
  components: {
      SearchHot,
      SearchHistoricalRecords,
      SearchList,
      SearchResult,
      SearchMusic,
  },

  data() {
    return {
      msg:'',
      searchProposal: null,
      isShowSearchList: false,
      searchHot: Array,
      songs: null,
      bestMatch: Object,
      isShowDetails : false,
      isShowClose : false,
      isDoAetails: false,
      historical:[],
      searchId:String,
    }
  },

  methods: {
    search(){
      if(!this.msg) return;
      this.$root.isShowLoading = true;
      this.axios
      .get("/search/suggest?keywords= "+this.msg+"&type=mobile")
      .then((response) => {
        this.searchProposal = response.data.result.allMatch;
      })
      .finally(() => {
        this.$root.isShowLoading = false;
      });
    
    },

    details(){
      if(!this.msg) return;
      // this.isDoAetails = false;

      if( this.historical.indexOf(this.msg) == -1 ){
        this.historical.push(this.msg);
      }

      this.isShowSearchList = false;
      this.isShowDetails = true;

      this.$root.isShowLoading = true;
      this.axios
      .get("/search?keywords= "+this.msg)
      .then((response) => {
        this.searchProposal = response.data.result.allMatch;

        this.songs = response.data.result.songs.map((item)=>{
          return{
            id: item.id,
            name: item.name,
            introduce: item.album.name,
            isShowIntroduce: true,
            song:{
              artists: item.artists
            }
          }
        });

        this.searchId = response.data.result.songs.slice(0, 20).map((e) => e.id).join();

        this.$root.isShowLoading = true;
        this.axios.get("/song/detail?ids=" + this.searchId).then((response) => {
            response.data.songs.forEach((item,index)=>{
              this.songs[index].picUrl=item.al.picUrl;
            });
          })
          .finally(() => {
            this.$root.isShowLoading = false;
          });

          this.$root.isShowLoading = true;
          this.axios.get("/search/multimatch?keywords= "+this.msg+"&type=mobile").then((response) => {
            this.bestMatch = response.data.result;
          })
          .finally(() => {
            this.$root.isShowLoading = false;
          });

      })
      .finally(() => {
        this.$root.isShowLoading = false;
      });
    },

    ItemFirst(item){
      this.msg = item;
      this.isDoAetails = true;
      this.isShowSearchList = false;
      this.isShowDetails = true;
      this.details();
    },

    removeList(removeItem){
      this.historical = this.historical.filter((item)=>{
        return item != removeItem;
      })
    },
  },

  created() {
      this.debouncedGetData = _.debounce(this.search, 200);

      this.$root.isShowLoading = true;
      this.axios
      .get("/search/hot")
      .then((response) => {
        this.searchHot = response.data.result.hots;
      })
      .finally(() => {
        this.$root.isShowLoading = false;
      });
    
  },

  watch: {
      msg: function () {
        this.isDoAetails = !this.isDoAetails;
        if(this.isDoAetails){
            this.isShowSearchList = true;
            this.debouncedGetData();
          }

          if(!this.msg){
            this.isShowSearchList = false;
            this.isShowDetails = false;
            this.isDoAetails = false;
          }

          this.isShowClose = (this.isShowSearchList || this.isShowDetails) ? true : false;
      },
  },
};
</script>

