<template>
    <div class="play-full-lyric" @click="$emit('toggle-show-lyric')">
        <ul class="lyric" 
            :style="styleObject" 
            @touchstart.stop="touchstart" 
            @touchmove.stop="touchmove"
            @touchend.stop="touchend"
        >
            <li 
                v-for="(item, index) in fullLyric" 
                :key="index" 
                :class="{currentColor:(index==currentIndex)}"
            >
                <span :class="{opacitys:(index==nextIndex)}" >{{item.times}}</span>
                <p>{{item.lyric}}</p>
            </li>
        </ul>
    </div>
</template>

<script>
export default {
    props:['currentMusic','currentTime'],
    data() {
        return {
            fullLyric: [],
            currentIndex: 0,
            y:0,
            touchTop : 0,
            currentTimes: 0,
            isTouch:false,
            touchIndex:0,
            nextIndex: null,
            moveSecond:0,
        }
    },
    methods: {
        obtainLyric(id){
            this.$root.isShowLoading = true;
            this.axios
            .get("/lyric?id="+id)
            .then((response) => {
                var arr = response.data.lrc.lyric.split('[');
                arr.shift();
                this.fullLyric = arr.map((item)=>{
                    var i = item.indexOf(']');
                    var times = item.substring(0, i);
                    times = times.substring(0,5);
                    var lyric = item.substring(i+1);
                    return{
                        times,
                        lyric,
                    }
                })
            })
            .finally(() => {
                this.$root.isShowLoading = false;
            });
        },

        lyricCurrentIndex(){
            this.fullLyric.forEach((item,index)=>{
                if( this.lyricCurrentTime ===  item.times ){
                    
                    if(this.isTouch){
                        this.currentIndex = index - 1;
                    }else{
                        this.currentIndex = index;
                    }
                }
            })
        },

        touchstart(e){
            this.moveSecond = 0;
            this.touchTop = 0;
            this.isTouch = true;
            this.y = e.touches[0].clientY;
            this.touchIndex = this.currentIndex;
        },
        touchmove(e){
            this.touchTop = Math.floor( this.y - e.touches[0].clientY );

            let fullLyricIndex = Math.floor(this.touchTop/31) + 1;
            this.nextIndex = fullLyricIndex + this.touchIndex;
            this.nextIndex = this.nextIndex < 0 ? 0 : this.nextIndex;

            if( this.nextIndex < 0 ){
                this.nextIndex = 0
            }else if( this.nextIndex > this.fullLyric.length-1 ){
                this.nextIndex = this.fullLyric.length-1;
            }

            let timesArr = this.fullLyric[this.nextIndex].times.split(':');
            this.currentLyricTimes = (timesArr[0]-0) * 60 + (timesArr[1]-0);
        },

        touchend(){
            if(!this.nextIndex)return;
            
            this.isTouch = false;
            this.nextIndex = null;

            this.$emit("current-lyric-times", this.currentLyricTimes)
        },
    },
    created(){
        this.obtainLyric(this.currentMusic.id);

        this.currentIndex = 0;
    },

    computed:{
        id(){
            return this.currentMusic.id;
        },
        
        lyricCurrentTime(){
            let minute;
            let second;
            minute = Math.floor(this.currentTime/60) < 10 ? "0"+ Math.floor(this.currentTime/60) : Math.floor(this.currentTime/60);
            second = Math.floor(this.currentTime%60) < 10 ? "0"+ Math.floor(this.currentTime%60) : Math.floor(this.currentTime%60);

            return minute + ":" + second;
        },

        tops(){
            if(this.isTouch){
                if( this.touchIndex * 31 - 200 + this.touchTop <= -250){
                    return -250;
                }else if( (this.touchIndex * 31 - 200 + this.touchTop) >= (this.fullLyric.length-1) * 31 -200){
                    console.log();
                    return (this.fullLyric.length-1) * 31 -200;
                }else{
                    return this.touchIndex * 31 - 200 + this.touchTop;
                    }
            }else{
                return this.currentIndex * 31 - 200;
            }
        },

        styleObject(){
            if(this.isTouch){
                return{
                    top: -(this.tops)+'px',
                    transition : 'all 0s',
                }
            }else{
                return{
                    top: -(this.tops)+'px',
                    transition : 'all 0.5s',
                }
            }
            
        }
    },

    watch:{
        id(id){
            this.obtainLyric(id);
        },

        currentTime(){
            this.lyricCurrentIndex();
        }

    }
}
</script>

<style scoped lang='less'>
    .play-full-lyric{
        width: 100vw;
        height: 420px;
        margin: 0 auto;
        position: absolute;
        top: 70px;
        left: 0;
        box-sizing: border-box;
        text-align: center;
        overflow: hidden;

        .lyric{
            width: 100%;
            height: 100%;
            position: absolute;
            top: 200px;
            transition: all 0s;

            li{
                font-size: 17px;
                padding-bottom: 8px;
                color: hsla(0,0%,100%,.6);
                display: flex;
                position: relative;
                span{
                    position: absolute;
                    font-size: 14px;
                    margin-left: 5px;
                    line-height: 20px;
                    color: rgb(199, 233, 7);
                    opacity: 0;

                    &::before{
                        content: "";
                        display: block;
                        width: 100%;
                        height: 1px;
                        background-image: linear-gradient(to left,rgba(255, 255, 255,0.8),transparent);
                        position: absolute;
                        top: 51%;
                        left: 310px;
                        z-index: -2;
                    }

                    &::after{
                        content: "";
                        display: block;
                        width: 100%;
                        height: 1px;
                        background-image: linear-gradient(to right,rgba(255, 255, 255,0.8),transparent);
                        position: absolute;
                        top: 51%;
                        left: 38px;
                        z-index: -2;
                    }
                }

                span.opacitys{
                    opacity: 1;
                }

                p{
                    flex: 1;
                }
            }
            li.currentColor{
                color: rgb(199, 233, 7);
            }
        }
    }
</style>